// 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 
import store from '@/store/index.js';
import { getSearchVoice } from '@/request/api/search.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 默认每次请求数量
      limit: 20,
      // 决定请求返回的数据范围
      offset: 0,
      // 当前页
      currentPage: 1,
      // 零
      zero: 0,
      // 搜索的关键词
      searchKey: '',
      // 接收的声音
      resources: [],
      // 查到的声音数量
      totalCount: 0,
      // html渲染的电台名
      vHtmlVoice: '',
      // 是否显示v-html嵌入的电台名关键字html标签
      isVHtmlVoice: 0
    };
  },

  mounted() {
    // 获取关键词
    this.searchKey = store.state.searchKey; // 搜索声音

    if (sessionStorage.getItem('searchKey')) this.getSearchVoice();
  },

  methods: {
    // 搜索声音(搜索声音返回字段格式会不一样？)
    async getSearchVoice() {
      let res = await getSearchVoice(sessionStorage.getItem('searchKey'), this.limit, this.offset); // console.log('搜索声音', res);

      this.totalCount = res.data.data.totalCount;
      this.resources = res.data.data.resources;
      if (this.resources.length !== 0) this.isLoading = 0; // console.log(this.resources);
    },

    // 换页
    pageChange() {
      this.offset = (this.currentPage - 1) * this.limit; // console.log('当前页码：', this.currentPage, '数据偏移量：', this.offset);
      // 重新搜索声音

      this.getSearchVoice();
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 声音关键词高亮显示
    activeVoice(string) {
      let reg = new RegExp(this.searchKey, 'g');
      let replaceString = '<span style="color: rgb(27, 94, 195)">' + this.searchKey + '</span>';
      string = string.replace(reg, replaceString);

      if (string.indexOf(this.searchKey) == -1) {
        this.isVHtmlVoice = 0;
        return string;
      } else {
        this.isVHtmlVoice = 1;
        this.vHtmlVoice = string;
        return '';
      }
    }

  }
};